































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from 'vue-property-decorator';

@Component
export default class Comhead extends Vue {
    @Prop({ required: false })
    isRightIcon: any;
    @Prop({ required: false })
    title: any;
    @Prop({ required: false })
    isLeftIcon: any;
    @Prop({ required: false })
    rightTitle: any;
    @Prop({ required: false })
    leftTitle: any;
    @Prop({ required: false })
    leftIconName: any;
    @Prop({ required: false })
    rightIconName: any;
    @Prop({ required: false })
    lineShow: any;
    @Prop({ required: false })
    skin: any;
    

  type = "H5";
  leftScale = "1.5";
  rightClick() {
    this.$emit("rightClick");
  }
  leftClick() {
    if(this.isLeftIcon == 'icon-zuo'){
      this.$router.go(-1);
      return
    }
    this.$emit("leftClick");
  }

  getBgColor() {}
  mounted() {
    console.log(this.title)
    console.log(this.isRightIcon)
  }
}



